.portfolio_container{
    display:grid;
    grid-template-columns: repeat( 1fr);
    gap:2.5rem;
}

.portfolio_item
{
    background: var(--color--bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio_item:hover{
    border-color: var(--color--primary-variant);
    background: transparent;
}

.portfolio_item-img{
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio_item h3{
    margin: 1.2 0 2rem;
    text-align: center;
}

.portfolio_item-cta{
    
    display:flex;
    gap:1rem;
    margin-bottom: 1rem;
}

/*-----------Media Queries for responsiveness <Medium devices>-----------------*/

@media screen and (max-width : 1024px){
   .portfolio_container{
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
   }
  }
  
  /*-----------Media Queries for responsiveness <Small devices>-----------------*/
  @media screen and (max-width : 600px){
    .portfolio_container{
        grid-template-columns: 1fr;
        gap: 1rem;
   }
  }

