header {
    height: 100vh;
    padding-top:7rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}



/*--------Download CV -------------*/

.downloadAction
{
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;
}

/*--------Header Socials -------------*/

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color--primary);
}

/*----------------------------------Me-----------------------------*/
.me{
    background: linear-gradient(var(--color--primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;

}

.circular--landscape { 
    margin-top: 7rem;
    display: inline-block;
     position: relative; 
     width: 300px;
     height: 300px; 
     overflow: hidden; 
     border-radius: 5px 10px 10px 10px; 
    } 
.circular--landscape img {
     width: auto; 
     height: 100%; 
     margin-left: 10%; 
     border-radius: 55px 55px 55px 55px; 
    }

/*----------------------------------Scroll-----------------------------*/
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/*-----------Media Queries for responsiveness <Medium devices>-----------------*/

@media screen and (max-width : 1024px){
    .header{
        height: 68vh;
    }

    .header_socials,.scroll_down{
        display: none;
    }
}

/*-----------Media Queries for responsiveness <Small devices>-----------------*/
@media screen and (max-width : 600px){
    .header{
        height: 100vh;
    }

    .header_socials,.scroll_down{
        display: none;
    }
}